import {CheckUserStatus, LoginApp} from "../../utils/common";
import {UserInfoModel} from "../../model/UserInfoModel";
import {GetLoginModel} from "../../model/LoginSuccessModel";

export default {
    onReady() {
        setTimeout(() => {
            if (!CheckUserStatus.isLogin()) return;

            uni.reLaunch({
                url: new LoginApp().getEnterPage()
            });
            if (new UserInfoModel().getModel("clientKey")) {
                console.error(GetLoginModel.getAccountSource());
                uni.setTabBarItem({
                    index: 0,
                    text : GetLoginModel.getAccountSource() == 2
                        ? "楼盘" : "统计",
                    iconPath : GetLoginModel.getAccountSource() == 2
                        ? "static/images/tabbar/build_default.png"
                        : "static/images/tabbar/tj_default.png",
                    selectedIconPath : GetLoginModel.getAccountSource() == 2
                        ? "static/images/tabbar/home_active.png"
                        : "static/images/tabbar/tj_active.png",
                    success: (res) => {
                        console.error(res, "--res--");
                    },
                    fail: (err) => {
                        console.error(err, "--err--");
                    }
                });
            }
        }, 500);
    }
}
